.cards {
  background: #fff;
  border-radius: 10px;
  margin: 15px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
  padding: 10px;
  border: solid 4px #2A77AE;
  border-radius: 6px;
}


.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: center;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 1rem;
  color: #2A77AE;
  margin-bottom: 30px;
  margin-top: 160px;
}

#winner {
  font-size: 30px;
  color: red;
  -webkit-text-decoration-color: aquamarine;
          text-decoration-color: aquamarine;
}
Nav {
  padding-top: 5px;   
  background-color: rgb(22, 63, 92);
  padding: 2px;
  font-size: 30px;  
  color:  white;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 1px;
  position: fixed;
  top: 0px;
  width: 100%;
}

html, body, #root {
  margin-top: 50px;
  padding: 0;
  margin: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

